#sidebar {
  border-right: 1px solid #0000002d;
  min-width: 250px;
  max-width: 250px;
  transition: all .3s;
}

#sidebar.active {
  margin-left: -250px;
}

#sidebar .sidebar-header {
  color: #fff;
  background: #323a46;
  border-bottom: 1px solid #0000002d;
  min-height: 58px;
  max-height: 58px;
  padding: .4rem 1rem;
}

#sidebar a[data-toggle="collapse"] {
  position: relative;
}

#sidebar .dropdown-toggle:after {
  display: none;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

#sidebar ul.components {
  padding: 0;
}

#sidebar ul p {
  padding: 10px;
}

#sidebar ul li a {
  padding: .8rem 1.5rem;
  font-size: 1rem;
  text-decoration: none;
  display: block;
}

#sidebar ul li a:hover, #sidebar ul li a.active {
  color: #fff;
  background: #2196f3;
}

#sidebar ul li.active > a, #sidebar ul li.active a[aria-expanded="true"] {
  color: inherit;
}

#sidebar ul ul a {
  font-size: 1rem;
}

@media (width <= 768px) {
  #sidebarCollapse span {
    display: none;
  }
}

#wrapper-content > .navbar {
  min-height: 58px;
  box-shadow: none;
  color: #fff;
  background: #323a46;
  border-bottom: 1px solid #0000002d;
  padding: 0 1.5rem;
}

.nav-dropdown .nav-link {
  line-height: 1.42857;
  padding: 1rem 0 1rem 1rem !important;
}

.nav-dropdown .nav-link:after {
  display: none;
}

.nav-dropdown .nav-link-menu {
  border: none;
  min-width: 220px;
  margin-top: -5px;
  padding: 0;
  line-height: 1.4;
  position: absolute;
  box-shadow: 0 1px 10px #455a6433;
}

.nav-dropdown .nav-link-menu:before {
  pointer-events: none;
  content: "";
  visibility: visible;
  z-index: 2;
  width: .5em;
  height: .5em;
  margin: 0 0 0 -.25em;
  display: block;
  position: absolute;
  top: -4px;
  right: 25%;
  transform: rotate(45deg);
}

.nav-dropdown .nav-link-menu .nav-list {
  margin-bottom: 0;
  padding: 5px 0;
  list-style: none;
}

.nav-dropdown .nav-link-menu .nav-list li {
  line-height: 1.2;
}

.nav-dropdown .nav-link-menu .nav-list li a {
  padding: .8rem;
  font-size: 14px;
}

.nav-dropdown .dropdown-divider {
  margin: 3px 0;
}

body {
  font-size: 1rem;
}

.wrapper {
  align-items: stretch;
  width: 100%;
  display: flex;
  overflow-x: hidden;
}

#wrapper-content {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  transition: all .3s;
}

#wrapper-content > .content {
  padding: .5rem;
  position: relative;
}

#wrapper-content .content .page-title h3 {
  margin: 1rem 0;
}

.page-header {
  margin-top: 1.25rem;
}

.page-pretitle {
  text-transform: uppercase;
  color: #656d77;
  font-size: .8rem;
  line-height: 1.6;
}

.page-title {
  margin: 0;
  font-size: 1.5rem;
  line-height: 1.55556;
}

.detail-subtitle {
  text-transform: uppercase;
  font-size: .8rem;
  line-height: 1.6;
}

.box {
  background: #fff;
  border-top: 3px solid #d2d6de;
  border-radius: 3px;
  width: 100%;
  margin-bottom: 20px;
  position: relative;
  box-shadow: 0 1px 1px #0000001a;
}

.box-body {
  border-radius: 0 0 3px 3px;
  padding: 10px;
}

.box-footer {
  text-align: right;
  background-color: #fafafa;
  border-top: 1px solid #f4f4f4;
  border-radius: 0 0 3px 3px;
  padding: 10px 20px;
}

.box-primary {
  border-top-color: #22a1f9;
}

.card .content {
  padding: 15px 15px 10px;
}

.card .content .icon-big {
  min-height: 64px;
  font-size: 3em;
  line-height: 64px;
}

.card .content .number {
  text-align: right;
  font-size: 1.5em;
  font-weight: bolder;
}

.card .content .footer {
  background-attachment: fixed;
  padding: 0;
  line-height: 30px;
  position: relative;
}

.card .content .stats {
  color: #a9a9a9;
  display: inline-block;
}

a, a:hover, a:focus {
  color: inherit;
  text-decoration: none;
  transition: all .3s;
}

.btn.focus, .btn:focus {
  box-shadow: none;
}

.btn.btn-square {
  border-radius: 0;
}

.table td, .table th {
  vertical-align: middle;
}

.nav-tabs {
  border-bottom: 2px solid #dee2e6;
}

.nav-tabs .nav-item {
  margin-bottom: 0;
}

.nav-tabs .nav-link {
  color: inherit;
  border: none;
  transition: color .1s;
}

.nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
  color: #007bff;
  background-color: #fff;
  border-bottom: 2px solid #22a1f9;
}

.tab-content {
  padding: 15px;
}

.svg-inline--fa {
  min-width: 15px;
}

.display-absolute {
  position: absolute;
}

.large-icon {
  font-size: 3em;
}

.license span {
  margin-bottom: 1em;
}

.teal {
  color: #00b5ad !important;
}

.olive {
  color: #b5cc18 !important;
}

.violet {
  color: #6435c9 !important;
}

.orange {
  color: #f2711c !important;
}

.darkgray {
  color: #a9a9a9 !important;
}

.blue {
  color: #2185d0 !important;
}

.grey {
  color: #767676 !important;
}

@media (width <= 768px) {
  .display-absolute {
    position: relative;
  }
}

@media (width <= 680px) {
  #body.active .navbar-collapse {
    flex-basis: auto;
    display: flex !important;
  }

  .nav-dropdown .nav-link-menu {
    margin-top: 0;
    width: 100% !important;
    position: fixed !important;
    top: 52px !important;
  }

  .nav-dropdown .nav-link {
    padding: 10px;
  }

  .nav-dropdown .nav-link-menu:before {
    right: 50%;
  }

  #body .navbar-collapse, #body .nav-dropdown .nav-item span {
    display: none !important;
  }

  .btn-header {
    display: none;
  }
}

@media (width >= 200px) {
  .navbar-expand-lg .navbar-collapse {
    flex-basis: auto;
    display: flex !important;
  }

  .navbar-expand-lg .navbar-nav {
    flex-direction: row;
  }
}

.btn-bd-primary {
  --bd-violet-bg: #712cf9;
  --bd-violet-rgb: 112.521, 44.0622, 249.438;
  --bs-btn-font-weight: 600;
  --bs-btn-color: var(--bs-white);
  --bs-btn-bg: var(--bd-violet-bg);
  --bs-btn-border-color: var(--bd-violet-bg);
  --bs-btn-hover-color: var(--bs-white);
  --bs-btn-hover-bg: #6528e0;
  --bs-btn-hover-border-color: #6528e0;
  --bs-btn-focus-shadow-rgb: var(--bd-violet-rgb);
  --bs-btn-active-color: var(--bs-btn-hover-color);
  --bs-btn-active-bg: #5a23c8;
  --bs-btn-active-border-color: #5a23c8;
}

.bd-mode-toggle {
  z-index: 1500;
}

.bd-mode-toggle .bi {
  width: 1em;
  height: 1em;
}

.bd-mode-toggle .dropdown-menu .active .bi {
  display: block !important;
}

svg.bi {
  vertical-align: -.125em;
  width: 16px;
  height: 16px;
  line-height: 1;
}

label.required:before {
  content: "* ";
  color: red;
}
